
.recipe{
    border-radius: 10px;
    box-shadow: 0px 5px 20px rgb(71, 70, 70);
    margin: 20px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: space-around;
    background: cadetblue;
    align-items: center;
    min-width: 40%;
  }

  .image{
      border-radius: 50%;
      width: 300px;
  }

